"use client";
import { useState } from "react";

export default function FiltersSection({ onFilterChange }) {
  const [year, setYear] = useState(2025);
  const [monthRange, setMonthRange] = useState("all");

  const applyFilters = () => {
    onFilterChange({ year, monthRange });
  };

  return (
    <div className="border border-green-300 bg-green-50 rounded-xl p-5 shadow-sm">

  <h2 className="font-semibold text-green-700 mb-3">
    Filters
  </h2>

  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
    {/* Year */}
    <div>
      <label className="text-sm text-gray-600">Year</label>
      <input
        className="w-full border rounded p-2 mt-1"
        type="number"
        value={year}
        onChange={(e) => setYear(e.target.value)}
      />
    </div>

    {/* Month Range */}
    <div>
      <label className="text-sm text-gray-600">Month Range</label>
      <select
        className="w-full border rounded p-2 mt-1"
        value={monthRange}
        onChange={(e) => setMonthRange(e.target.value)}
      >
        <option value="all">All Months</option>
        <option value="q1">Q1 (Janâ€“Mar)</option>
        <option value="q2">Q2 (Aprâ€“Jun)</option>
        <option value="q3">Q3 (Julâ€“Sept)</option>
        <option value="q4">Q4 (Octâ€“Dec)</option>
      </select>
    </div>
  </div>

  <button
    onClick={applyFilters}
    className="mt-4 bg-green-600 hover:bg-green-700 text-white px-5 py-2 rounded-lg"
  >
    Apply Filters
  </button>

    </div>
  );
}
