"use client";
import { useState } from "react";
import { Card, CardContent } from "@/components/ui/card";

// Format numbers as Philippine Peso
const currency = (val) => {
  const amount = val || 0;
  return new Intl.NumberFormat("en-PH", {
    style: "currency",
    currency: "PHP",
    minimumFractionDigits: 2,
  }).format(amount);
};

export default function TransactionTable({
  transactions,
  loading,
  onAddTransaction,
  onEditTransaction,
  onDeleteTransaction,
  setTransactions,
}) {
  const [form, setForm] = useState({
    date: "",
    description: "",
    deposit: "",
    withdrawal: "",
  });
  const [editingId, setEditingId] = useState(null);
  const [rowForm, setRowForm] = useState({});
  const [showForm, setShowForm] = useState(false);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm((prev) => ({ ...prev, [name]: value }));
  };

  const calculateBalances = (list) => {
    let runningBalance = 0;
    return list.map((t) => {
      runningBalance += (t.deposit || 0) - (t.withdrawal || 0);
      return { ...t, balance: runningBalance };
    });
  };
  const handleEditClick = (t) => {
  setEditingId(t.id);
  setRowForm({
    date: t.date,
    description: t.description,
    deposit: t.deposit,
    withdrawal: t.withdrawal,
  });
};



  const handleSubmit = (e) => {
  e.preventDefault();
  if (!form.date || (!form.deposit && !form.withdrawal)) {
    alert("Enter a date and at least deposit or withdrawal.");
    return;
  }

  const newTransaction = {
    ...form,
    deposit: Number(form.deposit || 0),
    withdrawal: Number(form.withdrawal || 0),
    id: Date.now(), // temporary ID
  };

  // Sort newest â†’ oldest ONLY for display
  const updated = [newTransaction, ...transactions].sort(
    (a, b) => new Date(b.date) - new Date(a.date)
  );

  // âŒ Remove this (causes your error)
  // onEditTransaction?.(updatedTransaction, id);

  // âœ… Keep this
  onAddTransaction?.(newTransaction);

  setForm({ date: "", description: "", deposit: "", withdrawal: "" });
  setShowForm(false);
};


const handleSave = (id) => {
  if (!rowForm.date || (!rowForm.deposit && !rowForm.withdrawal)) {
    alert("Enter a date and at least deposit or withdrawal.");
    return;
  }

  const updatedTransaction = {
    ...rowForm,
    deposit: Number(rowForm.deposit || 0),
    withdrawal: Number(rowForm.withdrawal || 0),
  };

  // âœ… Only call parent to update DB and recalc balances
  onEditTransaction(updatedTransaction, id);

  setEditingId(null);
  setRowForm({});
};


  const handleDelete = (id) => {
    const updated = transactions.filter((t) => t.id !== id);
    onDeleteTransaction?.(id);
  };


  const sortedTransactions = [...transactions].sort((a, b) => {
  const dateCompare = new Date(b.date) - new Date(a.date);
  if (dateCompare !== 0) return dateCompare;

  // If same date â†’ sort by ID (newest first)
  return (b.id ?? 0) - (a.id ?? 0);
});

  

  return (
    <Card>
      <CardContent className="space-y-4">
        <h2 className="text-lg font-semibold mb-3">Transaction Records</h2>

        <button
          onClick={() => setShowForm((prev) => !prev)}
          className="bg-blue-600 text-white rounded p-2 text-sm hover:bg-blue-700 transition mb-2"
        >
          {showForm ? "Hide Form" : "Add Transaction"}
        </button>

        {showForm && (
          <form
            onSubmit={handleSubmit}
            className="grid grid-cols-1 md:grid-cols-5 gap-2 items-end border-b border-gray-200 pb-4"
          >
            <div>
              <label className="block text-sm font-medium text-gray-700">Date</label>
              <input
                type="date"
                name="date"
                value={form.date}
                onChange={handleChange}
                className="mt-1 block w-full border rounded p-2 text-sm"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700">Description</label>
              <input
                type="text"
                name="description"
                value={form.description}
                onChange={handleChange}
                placeholder="e.g., Reimbursement"
                className="mt-1 block w-full border rounded p-2 text-sm"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700">Deposit (â‚±)</label>
              <input
                type="number"
                name="deposit"
                value={form.deposit}
                onChange={handleChange}
                min="0"
                className="mt-1 block w-full border rounded p-2 text-sm text-right"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700">Withdrawal (â‚±)</label>
              <input
                type="number"
                name="withdrawal"
                value={form.withdrawal}
                onChange={handleChange}
                min="0"
                className="mt-1 block w-full border rounded p-2 text-sm text-right"
              />
            </div>
            <div>
              <button
                type="submit"
                className="w-full bg-blue-600 text-white rounded p-2 text-sm hover:bg-blue-700 transition"
              >
                Add
              </button>
            </div>
          </form>
        )}

        {loading ? (
          <p>Loading...</p>
        ) : (
          <div className="overflow-x-auto">
            <table className="min-w-full border border-gray-300 text-sm">
              <thead className="bg-gray-100 text-gray-700 uppercase">
                <tr>
                  <th className="border p-2 text-left">Date</th>
                  <th className="border p-2 text-left">Description</th>
                  <th className="border p-2 text-right">Deposit (â‚±)</th>
                  <th className="border p-2 text-right">Withdrawal (â‚±)</th>
                  <th className="border p-2 text-right">Balance (â‚±)</th>
                  <th className="border p-2 text-center">Actions</th>
                </tr>
              </thead>
              <tbody>
                {sortedTransactions.map((t, index) => (
                  <tr key={t.id ?? index} className="bg-white hover:bg-gray-100">
                    {editingId === t.id ? (
                      <>
                        <td className="border p-2">
                          <input
                            type="date"
                            value={rowForm.date}
                            onChange={(e) =>
                              setRowForm({ ...rowForm, date: e.target.value })
                            }
                            className="border rounded p-1 text-sm w-full"
                          />
                        </td>
                        <td className="border p-2">
                          <input
                            type="text"
                            value={rowForm.description}
                            onChange={(e) =>
                              setRowForm({ ...rowForm, description: e.target.value })
                            }
                            className="border rounded p-1 text-sm w-full"
                          />
                        </td>
                        <td className="border p-2 text-right">
                          <input
                            type="number"
                            value={rowForm.deposit}
                            onChange={(e) =>
                              setRowForm({ ...rowForm, deposit: e.target.value })
                            }
                            className="border rounded p-1 text-sm w-full text-right"
                          />
                        </td>
                        <td className="border p-2 text-right">
                          <input
                            type="number"
                            value={rowForm.withdrawal}
                            onChange={(e) =>
                              setRowForm({ ...rowForm, withdrawal: e.target.value })
                            }
                            className="border rounded p-1 text-sm w-full text-right"
                          />
                        </td>
                        <td className="border p-2 text-right font-semibold text-blue-700">
                          {currency(t.balance)}
                        </td>
                        <td className="border p-2 text-center space-x-1">
                          <button
                            onClick={() => handleSave(t.id)}
                            className="bg-green-600 text-white px-2 py-1 rounded hover:bg-green-700 text-xs"
                          >
                            Save
                          </button>
                          <button
                            onClick={() => setEditingId(null)}
                            className="bg-gray-400 text-white px-2 py-1 rounded hover:bg-gray-500 text-xs"
                          >
                            Cancel
                          </button>
                        </td>
                      </>
                    ) : (
                      <>
                        <td className="border p-2">
                          {new Date(t.date).toLocaleDateString("en-PH", {
                            month: "short",
                            day: "numeric",
                            year: "numeric",
                          })}
                        </td>
                        <td className="border p-2">{t.description}</td>
                        <td className="border p-2 text-right">{currency(t.deposit)}</td>
                        <td className="border p-2 text-right text-red-600">
                          {currency(t.withdrawal)}
                        </td>
                        <td className="border p-2 text-right font-semibold text-blue-700">
                          {currency(t.balance)}
                        </td>
                        <td className="border p-2 text-center space-x-1">
                          <button
                            onClick={() => handleEditClick(t)}
                            className="bg-yellow-400 text-white px-2 py-1 rounded hover:bg-yellow-500 text-xs"
                          >
                            Edit
                          </button>
                          <button
                            onClick={() => handleDelete(t.id)}
                            className="bg-red-600 text-white px-2 py-1 rounded hover:bg-red-700 text-xs"
                          >
                            Delete
                          </button>
                        </td>
                      </>
                    )}
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </CardContent>
    </Card>
  );
}
