export default function MonthlyBreakdownTable({ data }) {
  if (!data || data.length === 0) return null;

  let running = 0;

  return (
    <div className="bg-white shadow-md rounded-xl p-5 border mt-6">
      <h2 className="font-semibold text-lg mb-4">Monthly Breakdown</h2>

      <table className="w-full border-collapse">
        <thead>
          <tr className="bg-gray-100 text-left">
            <th className="p-2 border">Month</th>
            <th className="p-2 border">Income</th>
            <th className="p-2 border">Expense</th>
            <th className="p-2 border">Net</th>
            <th className="p-2 border">Running Balance</th>
          </tr>
        </thead>

        <tbody>
          {data.map((row, idx) => {
            const net = row.income - row.expense;
            running += net; // UPDATE RUNNING BALANCE

            return (
              <tr key={idx} className="hover:bg-gray-50">
                <td className="p-2 border">{row.month}</td>
                <td className="p-2 border text-green-600">â‚±{row.income.toLocaleString()}</td>
                <td className="p-2 border text-red-600">â‚±{row.expense.toLocaleString()}</td>
                <td className="p-2 border font-semibold">â‚±{net.toLocaleString()}</td>
                <td className="p-2 border">â‚±{running.toLocaleString()}</td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}
